
import axios from 'axios'
console.log('12312net')


var baseUrl = 'http://localhost:8080/bam'

//axios post 请求
//@param action: 请求类型
//@param dat:请求的数据，json格式，不能嵌套多级，即{a:{b:1}} 这样是不允许的
function post(action, dat, func) {
    var data = {
        a: action,
        t: '111111',
        d: dat
    }
    axios({
            method: 'post',
            url: baseUrl,
            data: data,
            withCredentials: false, //是否携带cookie
            headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
        })
        .then(function(response) {
            console.log("response", response)
            // func(JSON.parse(response.request.response))
        }.bind(this))
}

function get() {
    axios.get(baseUrl + '/prj_info_update', {
    params: {
      prjName: 'asd'
    }
  })
  .then(function (response) {
    console.log(response);
  })
  .catch(function (error) {
    console.log(error);
  });
}

export default {
    post, get
}



